import { Component, OnInit, ViewChild, ElementRef, Inject } from '@angular/core';
import { AppComponent } from '../../app/app.component';
import { ProductService } from '../services/product.service';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { trigger, animate, transition, style } from '@angular/animations';
import { Env } from '../objects/env';
import { keys } from 'config';
import { VirtualScrollerComponent } from 'ngx-virtual-scroller';

@Component({
  selector: 'app-productlist',
  templateUrl: './productlist.component.html',
  styleUrls: ['./productlist.component.css'],
  animations: [
    trigger(
      'popupAnim', [
        transition('void => *', [
          style({ transform: 'translateY(-100%)', opacity: 0 }),
          animate('400ms', style({ transform: 'translateY(0)', opacity: 1 }))
        ]),
        transition('* => void', [
          style({ transform: 'translateY(0)', opacity: 1 }),
          animate('400ms', style({ transform: 'translateY(100%)', opacity: 0 }))
        ])
      ]
    )]
})

export class ProductlistComponent implements OnInit {

  @ViewChild(VirtualScrollerComponent, { static: true })
  scroll: VirtualScrollerComponent;
  @ViewChild('myInput', { static: false }) myInput: ElementRef;
  filteredItems: any[] = null;
  // fixed:boolean = false;
  scrollPos = 0;
  btn_chk: boolean = false;
  AWS = require('aws-sdk');
  private env = new Env;
  // scrollVarTop:number = 18;
  scrollVarBot: number = 0;
  filterString: string = null;
  // cardHeight:number;

  constructor(
    @Inject(AppComponent) public parent: AppComponent,
    public prodService: ProductService,
    private router: Router,
    public auth: AuthService
  ) {
    this.env.bucket = keys.aws_bucket;
    this.env.id = keys.aws_id;
    this.env.key = keys.aws_key;
    this.prodService.mda = false;
    this.prodService.mda2 = true;
    this.prodService.mda3 = false;
    this.prodService.mda4 = false;
  }

  ngOnInit() {
    window.addEventListener('scroll', (el)=>{
      this.scrollPos = el['target']['scrollingElement']['scrollTop'];
      if (el['target']['scrollingElement']['scrollTop'] > 1000) {
        // this.auth.navBar.next(false);
        this.btn_chk = true;
      } else {
        // this.auth.navBar.next(true);
        this.btn_chk = false;
      }
    });
    if (localStorage['search']) {
      this.myInput.nativeElement.value = localStorage['search'];
      this.parent.prods = this.parent.prods.filter(item => item.attributes.title.toLowerCase().indexOf(localStorage['search'].toLowerCase()) > -1);
      localStorage.removeItem('search');
    }
    if (localStorage['scroll']) {
      // console.log(localStorage['scroll']);
      if (localStorage['scroll'] > 500) {
        window.scrollTo(0, 1000);
      }
      this.scroll.scrollToPosition(Number(localStorage['scroll']), 1);
      localStorage.removeItem('scroll');
    }


  }

  filterItemDel() {
    this.myInput.nativeElement.value = "";
    this.filterItem("");
  }

  addToBucket(prod_id) {
    if (this.parent.bucket.product_id[prod_id]) {
      this.parent.bucket.product_id[prod_id]++
    } else {
      this.parent.bucket.product_id[prod_id] = 1
    }
    this.prodService.addToBucket(this.parent.bucket).subscribe(res => {
      this.prodService.bucket.next(res);
    })
  }

  goToShow(prod): void {
    let postLink = ['/products', prod.id];
    this.router.navigate(postLink);
  }

  filterItem(value) {
    this.prodService.catItems.next(null);
    this.scrollVarBot = 0;
    this.parent.category = null;
    if (!value) {
      this.parent.prods = this.parent.clone;
    } else {
      this.parent.prods = this.parent.prods.filter(item => item.attributes.title.toLowerCase().indexOf(value.toLowerCase()) > -1);
    }
  }

  gooUp() {
    window.scrollTo(0, 0);
    // this.scroll.scrollToIndex(0, true, 0, 0);
    this.scrollVarBot = 0;
  }

  ngOnDestroy() {
    localStorage.setItem("scroll", this.scrollPos.toString());
    if (this.myInput.nativeElement.value) localStorage.setItem("search", this.myInput.nativeElement.value);
  }

  deleteProd(prod) {
    if (window.confirm('Вы уверены что хотите удалить этот продукт?')) {
      let self = this;
      this.AWS.config.update({ region: 'ap-southeast-1', credentials: { "accessKeyId": this.env.id, "secretAccessKey": this.env.key } });
      prod.attributes.images.forEach(function (element) {
        let s3 = new self.AWS.S3().deleteObject({ Bucket: self.env.bucket, Key: element.split('/')[element.split('/').length - 2] + '/' + element.split('/')[element.split('/').length - 1] }, function (err, data) {
        });
      });
      this.prodService.deleteProd(prod.id)
        .subscribe(res => {
          this.router.navigate(['/products']);
        },
          err => alert(err)
        );
    }
  }
}
