import { Component, OnInit, HostListener, Inject } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { ProductService } from '../services/product.service';
import { MaterializeDirective } from "angular2-materialize";
import { AuthService } from '../services/auth.service';
import { Env } from '../objects/env';
import { keys } from 'config';
import { trigger, animate, transition, style } from '@angular/animations';
import { AppComponent } from '../../app/app.component';

@Component({
  selector: 'app-productshow',
  templateUrl: './productshow.component.html',
  styleUrls: ['./productshow.component.css'],
  animations: [
    trigger(
      'popupAnim', [
        transition('void => *', [
          style({ transform: 'translateY(-100%)', opacity: 0 }),
          animate('400ms', style({ transform: 'translateY(0)', opacity: 1 }))
        ]),
        transition('* => void', [
          style({ transform: 'translateY(0)', opacity: 1 }),
          animate('400ms', style({ transform: 'translateY(100%)', opacity: 0 }))
        ])
      ]
    ),
    trigger(
      'enterAnimation', [
        transition('void => *', [
          style({ opacity: 1 }),
          animate('600ms', style({ opacity: 1 }))
        ]),
        transition('* => void', [
          style({ opacity: 1 }),
          animate('600ms', style({ opacity: 0 }))
        ])
      ]
    )
  ]
})
export class ProductshowComponent implements OnInit {

  id: number;
  AWS = require('aws-sdk');
  private env = new Env;
  scrollPos = 0;
  Counter = 0;
  btn_chk: boolean = false;

  constructor(public prodService: ProductService,
    private route: ActivatedRoute,
    private router: Router,
    public auth: AuthService,
    @Inject(AppComponent) public parent: AppComponent

  ) {
    this.prodService.prod.next(null);
    // this.http.get('https://rocky-brook-32856.herokuapp.com/env').subscribe(data=>{this.env = data.json();});

  }

  deleteProd(id: number) {
    if (window.confirm('Вы уверены что хотите удалить этот продукт?')) {
      let self = this;
      this.AWS.config.update({ region: 'ap-southeast-1', credentials: { "accessKeyId": this.env.id, "secretAccessKey": this.env.key } });
      this.parent.prod.images.forEach(function (element) {
        let s3 = new self.AWS.S3().deleteObject({ Bucket: self.env.bucket, Key: element.split('/')[element.split('/').length - 2] + '/' + element.split('/')[element.split('/').length - 1] }, function (err, data) {

        });
      });
      this.prodService.deleteProd(id)
        .subscribe(res => {
          this.router.navigate(['/products']);
        },
          err => alert(err)
        );
    }
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.prodService.mda2 = false;
    this.prodService.mda = false;
    this.prodService.mda3 = false;
    this.prodService.mda4 = false;
    this.route.params.subscribe(
      params => {
        this.id = params['id'];
        this.prodService.getProd(this.id).subscribe(res => {
          this.prodService.prod.next(res['data']['attributes']);
        })
      })
    this.env.bucket = keys.aws_bucket;
    this.env.id = keys.aws_id;
    this.env.key = keys.aws_key;
  }

  addToBucket(prod_id) {
    if (this.parent.bucket.product_id[prod_id]) {
      this.parent.bucket.product_id[prod_id]++
    } else {
      this.parent.bucket.product_id[prod_id] = 1
    }
    this.prodService.addToBucket(this.parent.bucket).subscribe(res => {
      this.prodService.bucket.next(res);
    })
  }

  ngOnDestroy() {
    this.prodService.prod.next(null);
  }

  flooring(number) {
    return Math.floor(12 / number);
  }

  @HostListener('window:scroll', ['$event'])
  onWindowScroll() {
    const offset = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    if (offset > this.scrollPos) {
      this.Counter += 1
    } else {
      this.Counter -= 1
    }
    this.scrollPos = offset;
    if (this.scrollPos > 500) { this.btn_chk = true } else { this.btn_chk = false }
  }

  gooUp() {
    window.scrollTo(0, 0);
  }

}
