import { Injectable, EventEmitter} from '@angular/core';
import {Observable} from "rxjs";
import {map} from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {MaterializeAction} from "angular2-materialize";
import {Bucket} from '../objects/bucket';
import {BehaviorSubject} from 'rxjs';



@Injectable()
export class ProductService {
  carousel:any;
  actions = new EventEmitter<string>();
  news = new BehaviorSubject(null);
  headers: HttpHeaders;
  private prodsUrl = 'https://rocky-brook-32856.herokuapp.com/products';
  private mainUrl = 'https://rocky-brook-32856.herokuapp.com/';
  // https://rocky-brook-32856.herokuapp.com/
  public modalActions = new EventEmitter<string|MaterializeAction>();
  public modalActionsbla = new EventEmitter<string|MaterializeAction>();
  modeDial:  'new' | 'edit' = 'new';
  bucket = new BehaviorSubject(null);
  mda:boolean = false;
  sum = new BehaviorSubject(null);
  shprod = new BehaviorSubject(null);
  mda2:boolean = false;
  mda3:boolean = false;
  mda4:boolean = false;
  bucket_numb = new BehaviorSubject(null);
  scrollVarTop:number = 18;
  scrollVarBot:number = -1; 
  countDB:number;
  prod = new BehaviorSubject(null);
  prods = new BehaviorSubject(null);
  cats = new BehaviorSubject(null);
  catItems = new BehaviorSubject(null);

  constructor(private http:HttpClient) {
    this.mda = false; 
  }

  createAuthHeaders(){
    this.headers = new HttpHeaders();
    this.headers = this.headers.append('Content-Type', 'application/json');
    // this.headers = this.headers.append('authorization', this.auth.authToken);
  }

  getFirst(id): Observable<Partial<any>> {
    return this.http.get(this.mainUrl+"first/"+id)
      .pipe(map((response: Response) => response))
  }

  getProds(): Observable<Partial<any>> {
    return this.http.get(this.prodsUrl)
    .pipe(map((response: Response) => response))
  }

  getNews(){
    return this.http.get(this.prodsUrl+'2')
      .pipe(map((response: Response) => response))
  }

  createProd(prod){
    this.createAuthHeaders()
    return this.http.post(this.prodsUrl,JSON.stringify(prod),{headers:this.headers})
      .pipe(map((res:Response) => res["data"]["attributes"]));
  }

  getProd(id) {
    return this.http.get(this.prodsUrl+"/"+id)
  }

  deleteProd(id){
    this.createAuthHeaders()
    return this.http.delete(this.prodsUrl+'/'+id, {headers:this.headers})
  }

  updateProd(prod:any){
    this.createAuthHeaders()
    const url = `${this.prodsUrl}/${prod.id}`;
    return this.http.put(url,JSON.stringify(prod),{headers:this.headers}).pipe(map((res: Response) => res["data"]["attributes"]))
  }

  openModal(mode: 'new' | 'edit' = 'new') {
    this.modeDial = mode;
    this.modalActions.emit({action:"modal",params:['open']});
  }

  closeModal() {
      this.modalActions.emit({action:"modal",params:['close']});
  }

  openModal2() {
      this.modalActionsbla.emit({action:"modal",params:['open']});
  }

  closeModal2() {
      this.modalActionsbla.emit({action:"modal",params:['close']});
  }

  createBucket(id){
    this.createAuthHeaders()
    return this.http.post(this.mainUrl+'buckets',{user_id: id},{headers:this.headers})
      .pipe(map((response: Response) => response))
  }

  getBucket(user_id){
    return this.http.get(this.mainUrl+'buckets/'+user_id).pipe(map((response: Response) => response))
  }

  newBucket(): Observable<any>{
    return this.http.get(this.mainUrl+'bucketsnew')
      .pipe(map((response: Response) => response))
  }

  addToBucket(obj){
    this.createAuthHeaders()
    return this.http.post(this.mainUrl+'buckets/'+obj.user_id,obj,{headers:this.headers})
      .pipe(map((response: Response) => response))
  }

  delFromBucket(id,prod_id){
    this.createAuthHeaders()
    return this.http.post(this.mainUrl+'buckets2/'+id,{product_id: prod_id},{headers:this.headers})
      .pipe(map((response: Response) => response))
  }

  // remFromBucket(prod_id){
  //   return this.http.post(this.mainUrl+'delFromBucket/'+prod_id,{product_id: prod_id},this.options)
  //     .map((response: Response) => response)
  // }

  deleteBucket(id:number){
    this.createAuthHeaders()
    return this.http.delete(this.mainUrl+'buckets/'+id, {headers:this.headers})
  }

  cleanBucket(id){
    return this.http.get(this.mainUrl+'cleanbucket/'+id)
  }


  smdispbucket(prod:any){
    this.shprod.next(prod);
    
    window.setTimeout(() => {
      this.shprod.next(null);
      
    }, 4000);
  }

    isNewMode(){return this.modeDial == 'new'}
    isEditMode(){return this.modeDial == 'edit'}
  
}