import { Component, OnInit, EventEmitter, ViewChild,HostListener, Inject} from '@angular/core';
import {MaterializeAction} from "angular2-materialize";
import {ProductService} from '../services/product.service';
import {AuthService} from '../services/auth.service';
import {Router} from '@angular/router';
import {AppComponent} from '../../app/app.component';



@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
  
})
export class MainComponent implements OnInit {

	@ViewChild('carousel',{static:false}) carouselEl:any;
	urls = [
		'assets/ede19b_83f0a166404c485e8cc9ef6f876867ba_mv2.webp',
		'assets/ede19b_e1952d33e4604f529b77bd7b5bdb9fee_mv2.webp',
		'assets/ede19b_37e51eaedb064f98af07f62cc7c52a0a_mv2.webp'
	];
  	actions = new EventEmitter<string|MaterializeAction>();
  	prodsloc:any[] = [];
    // prods = null;
    bucket = null;
    user = null;
    news = null;
    fake = null;

  constructor(
    public prodService:ProductService, 
    public auth:AuthService,    
    private router:Router,
    @Inject(AppComponent) public parent: AppComponent
) { 
    this.prodService.mda2 = false;
    this.prodService.mda3 = false;
    this.prodService.mda4 = false;
    // this.prodService.prods.subscribe(res=>this.prods=res);
  	setTimeout(()=>{
	    this.actions.emit('carousel');
      this.carouselEl.nativeElement.classList.toggle('initialized');
    },2000)
    setInterval(()=>{
      this.theBegin();
    },5000)
  }

  @HostListener ('window:resize' ,['$event'])
  onWindowResize() {
    setTimeout(()=>{
	    this.actions.emit('carousel');
      this.carouselEl.nativeElement.classList.toggle('initialized');
    },2000)
  }

  theBegin(){
  	this.actions.emit({ action: 'carousel', params: ['next'] })
  }

  ngOnInit() {
    this.auth.fake.subscribe(res=>this.fake=res)
    this.prodService.bucket.subscribe(res=>{
        this.bucket = res;
      
    })
    this.auth.user.subscribe(res=>{
        this.user = res;
      
    })
    this.prodService.news.subscribe(res=>{
        this.news=res
      
    });
  }

  goToShow(prod):void {
    let postLink = ['/products', prod.id];
    this.router.navigate(postLink);
  }

  addToBucket(prod_id){
    if(this.bucket.product_id[prod_id]){
      this.bucket.product_id[prod_id]++
    }else{
      this.bucket.product_id[prod_id] = 1
    }
    this.prodService.addToBucket(this.bucket).subscribe(res=>{
      this.prodService.bucket.next(res);
    })
  }
}
