import {Component, OnInit, ViewChild, Inject} from '@angular/core';
import {AuthDialogComponent} from "../auth-dialog/auth-dialog.component";
import {AuthService} from "../services/auth.service";
import {Router} from "@angular/router";
import {ProductService} from '../services/product.service';
import {trigger, animate, transition, style} from '@angular/animations';
import {OrderService} from '../services/order.service';
import {AppComponent} from '../../app/app.component';


@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.css'],
  animations: [
     trigger(
      'popupAnim', [
        transition('void => *', [
          style({transform: 'translateY(100%)',opacity: 0}),
          animate('500ms', style({transform: 'translateY(0)',opacity: 1}))
        ]),
        transition('* => void', [
          style({transform: 'translateY(0)',opacity: 1}),
          animate('500ms', style({transform: 'translateY(-100%)',opacity: 0}))
        ])
        ]
        ),
        trigger(
          'popupAnim2', [
            transition('void => *', [
              style({opacity: 0}),
              animate('500ms', style({opacity: 1}))
            ]),
            transition('* => void', [
              style({opacity: 1}),
              animate('500ms', style({opacity: 0}))
            ])
            ]
            )
      ]
})
export class ToolbarComponent implements OnInit {

  @ViewChild('authDialog',{static:false}) authDialog: AuthDialogComponent;
  shprod = null;

  constructor(
    public orderService:OrderService,
    public authService:AuthService,
    private router:Router,
    public prodService:ProductService,
    @Inject(AppComponent) public parent: AppComponent
    ) {
    this.prodService.mda = false;
    this.prodService.shprod.subscribe(res=>{
      this.shprod=res;
      // console.log(res)
    })
  }

  abc(val,arr):boolean {
      for (let i = 0; i < arr.length; i+=1){
        if (val == arr[i]){return true}else{}
      }
      return false;
    }

  ngOnInit(){
  }

  logOut(){
    let self = this;
    this.authService.logOutUser().subscribe(() => {
      if(this.prodService.mda){this.router.navigate(['/']);};
      this.prodService.bucket.next(null);
      this.authService.user.next(null);
      if (localStorage['ognetushitely_user_id']){
        this.parent.fake = {id:Number(localStorage['ognetushitely_user_id'])};
        this.authService.fake.next(this.parent.fake);
        this.prodService.getBucket(localStorage['ognetushitely_user_id']).subscribe(res=>{
              if (res["id"]){
                this.prodService.bucket.next(res);
                // res.forEach(function(el){
                //   self.sum = self.sum + el.product.price*el.number;
                //   self.bucket_numb = self.bucket_numb + el.number;
                //   this.prodService.sum.next(self.sum);
                //   this.prodService.bucket_numb.next(self.bucket_numb);
                // })
              }else{
                this.parent.fake = {id:res};
                this.authService.fake.next(this.parent.fake);
                localStorage['ognetushitely_user_id'] = res;
                // localStorage['ognetushitely_bucket_id'] = res.bucket_id;
                
              }
        })
      }else{
        this.prodService.newBucket().subscribe(res=>{
          this.parent.fake = {id:res};
          this.authService.fake.next(this.parent.fake);
          localStorage['ognetushitely_user_id'] = res;
          // localStorage['ognetushitely_bucket_id'] = res.bucket_id;
        })
      }
      
    })
  }

  gooUp(){
    window.scrollTo(0,0);
    this.prodService.scrollVarTop = 18;
    this.prodService.scrollVarBot = -1;
  }

  presentAuthDialog(mode?: 'login'| 'register'){
    this.authDialog.openDialog(mode);

  }

  bucketNav(){
    if (this.parent.bucket && this.parent.bucket_numb > 0){
      this.router.navigate(['/bucket'])
    }else{alert('Ваша корзина пуста')}
  }

}