import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {AuthService} from "../services/auth.service";
import {ProductService} from '../services/product.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register-form',
  templateUrl: './register-form.component.html',
  styleUrls: ['./register-form.component.css']
})
export class RegisterFormComponent implements OnInit {

  signUpUser = {
    login: '',
    password: '',
    passwordConfirmation: '',
    userType: ''
  };

  @Output() onFormResult = new EventEmitter<any>();

  constructor(public authService:AuthService, private prodService:ProductService, private router:Router) { }

  ngOnInit() {}


  onSignUpSubmit(){
    this.signUpUser.userType = 'USER';
    this.authService.registerUser(this.signUpUser).subscribe((res) => {
          if (res.status == 200){
            this.onFormResult.emit({signedUp: true, res})
            this.authService.user.next(this.authService.userData());
            this.authService.fake.next(null);
            this.router.navigate(['/'])

            this.prodService.createBucket(res['body']["data"]["id"]).subscribe(res=>{this.prodService.bucket.next(res)});
          }

        },

        (err) => {
          console.log(err)
          this.onFormResult.emit({signedUp: false, err})
        }
    )

  }
}