import { Injectable } from '@angular/core';
import {AngularTokenService} from "angular-token";
import {Subject, Observable} from "rxjs";
import {User} from '../objects/user';
import {Router} from '@angular/router';
import {ProductService} from '../services/product.service';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class AuthService {

  userSignedIn$:Subject<boolean> = new Subject();
  user = new BehaviorSubject(null);
  fake = new BehaviorSubject(null);

  constructor(
    public authService:AngularTokenService, 
    private router:Router,
    private prod:ProductService
  ){

  }

  validateToken(){
    return this.authService.validateToken()
  }

  userType(){
    return this.authService.currentUserType
  }

  userData(){
    return this.authService.currentUserData
  }

  logOutUser():Observable<any>{
    return this.authService.signOut()
  }

  registerUser(signUpData:  {login:string, password:string, passwordConfirmation:string, userType?:string}):Observable<Response>{
    return this.authService.registerAccount(signUpData)
  }

  logInUser(signInData: {login:string, password:string, userType?:string}):Observable<Response>{
    return this.authService.signIn(signInData)
  }

  deleteUser(){
   return this.authService.deleteAccount()
 }

}