import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import {ProductService} from '../services/product.service';
import {Env} from '../objects/env';
import { Ng2PicaService } from 'ng2-pica';
import * as $ from 'jquery';
import {AuthService} from '../services/auth.service';
import {keys} from 'config';

@Component({
  selector: 'app-productedit',
  templateUrl: './productedit.component.html',
  styleUrls: ['./productedit.component.css']
})
export class ProducteditComponent implements OnInit {

  private env = new Env;
  AWS = require('aws-sdk');
  sucess:boolean = false;
  img_upload:boolean = false;
  urls:string[] = [];
  err:string;
  prod = null;

	@Output() onFormResult = new EventEmitter<any>();

  constructor(public prodService:ProductService,
    private pic:Ng2PicaService,
    private auth:AuthService
) { 
   
     // this.http.get('https://rocky-brook-32856.herokuapp.com/env').subscribe(data=>{this.env = data.json();});
   this.env.bucket = keys.aws_bucket;
       this.env.id = keys.aws_id;
       this.env.key = keys.aws_key;
       this.AWS.config.update({region: 'ap-southeast-1', credentials: {"accessKeyId": this.env.id, "secretAccessKey": this.env.key}});

  }

  ngOnInit() {
    this.prodService.prod.subscribe(res=>this.prod=res)
  }

  update(prod){
    if(prod.category){prod.category = prod.category.toLowerCase();}
    // if(prod.title ){prod.title = prod.title.toLowerCase();}
  	this.prodService.updateProd(prod).subscribe(res=>{
      // console.log(res);
  		this.prodService.prod.next(res);
  		if(res){this.onFormResult.emit({signedIn: true, res});}
  	},
  	err=>{
  		console.log('err:', err);
        this.onFormResult.emit({signedIn: false, err});
  	})
  }

  getFile(fileInput:any){
    let self = this;
    let file = fileInput.target.files;
    for (let i=0; i < file.length; i++){
      this.pic.resize([file[i]], 800, 800,true).subscribe(res=>this.fileEvent(res));
    }
  }

  fileEvent(data:any){
    this.sucess = false;
    this.img_upload = true;
    let self = this;
    let params = {Bucket: this.env.bucket, Key: this.prod.id+'/'+this.makeid(), Body: data};
    let s3 = new this.AWS.S3.ManagedUpload({params: params});
    s3.on('httpUploadProgress', function(evt) {
      $('#pus').css('width','0%');
      $('#pus').css('width',evt.loaded*100/evt.total+'%');
    }).send(function(error, s3res) { 
      if (error) {self.err = error.message;}else{
        self.sucess = true;
        self.prod.images[self.prod.images.length] = 'https://s3-ap-southeast-1.amazonaws.com/'+self.env.bucket+'/'+s3res.Key;
        self.prodService.updateProd(self.prod).subscribe(res=>{
          self.prodService.prod.next(res);
        })
      };
    });
  }

  delImg(idx){
    let self = this;
    let s3 = new this.AWS.S3().deleteObject({Bucket: this.env.bucket, Key: this.prod.id+'/'+this.prod.images[idx].split('/')[this.prod.images[idx].split('/').length-1]},function(err, data) {
      if (data){
        self.prod.images.splice(idx, 1);
        self.prodService.updateProd(self.prod).subscribe(res=>{
          self.prodService.prod.next(res);
        })
      }else{console.log(err)}
    }); 

  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 20; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }

}
