import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {AuthService} from "../services/auth.service";
import {ProductService} from '../services/product.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.css']
})
export class LoginFormComponent implements OnInit {

  signInUser = {
    login: '',
    password: '',
    userType: ''
  };

  @Output() onFormResult = new EventEmitter<any>();
  
  constructor(public authService:AuthService, private prodService:ProductService, private router:Router) {}

  ngOnInit() {}

  onSignInSubmit(){
  	if (this.signInUser.login == 'apella2010@gmail.com'){this.signInUser.userType = 'ADMIN'}else{this.signInUser.userType = 'USER'}
    this.authService.logInUser(this.signInUser).subscribe(
        res => {
          // console.log(res)
          if(res.status == 200){
            this.onFormResult.emit({signedIn: true, res});
            let user = <any>this.authService.userData();
            if(this.authService.userType()=='ADMIN'){
              user.type = true;
            }
            // console.log(user)
            this.authService.user.next(user);
            this.authService.fake.next(null);
            this.router.navigate(['/'])
            if(this.authService.userType()){

            }else{
              this.prodService.getBucket(res['body']['data']['id']).subscribe(res=>{
                this.prodService.bucket.next(res);
              });
            }
          }
        },
        err => {
          console.log('err:', err);
          this.onFormResult.emit({signedIn: false, err});
        }
    );

  }

}