import { Component, OnInit, Inject } from '@angular/core';
import {ProductService} from '../services/product.service';
import {Order} from '../objects/order';
import {AuthService} from '../services/auth.service';
import {OrderService} from '../services/order.service';
import {Router} from '@angular/router';
import {AppComponent} from '../../app/app.component';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  order = new Order;
  innerWidth: any;

  constructor(
    public prodService:ProductService, 
    public auth:AuthService, 
    private orderService:OrderService, 
    private router:Router,
    @Inject(AppComponent) public parent: AppComponent

  ) {
   }

  ngOnInit() {
  }

  createOrder(order){

  	order.price = this.parent.sum;
    order.user_id = this.parent.user? this.parent.user.id : this.parent.fake.id;
    if(!order.user_email){
      if (this.parent.user && this.parent.user.email){
        order.user_email = this.parent.user.email
      }
    }
    // console.log(order)
  	this.orderService.createOrder(order).subscribe(res=>{
      // console.log(res)
  		this.orderService.orders.next(res);
  		this.orderService.closeModal();
      this.prodService.cleanBucket(this.parent.user? this.parent.user.id : this.parent.fake.id).subscribe();
  		this.prodService.bucket.next([]);
  		this.router.navigate(['/']);
  	})
  }
}
