import { Component, OnInit } from '@angular/core';
import {ProductService} from '../services/product.service';
import {Router} from '@angular/router';
import {AuthService} from '../services/auth.service';
import {Env} from '../objects/env';
import {Product} from '../objects/product';
import {keys} from 'config';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

AWS = require('aws-sdk');
  private env = new Env;
  news:any[]=[];
  user = null;

  constructor(public prodService:ProductService, private router:Router, public auth:AuthService
 ) { 	
    this.prodService.news.subscribe(res=>this.news=res);
    this.prodService.mda2 = false;
  	this.prodService.mda = false;
    this.prodService.mda3 = false;
    this.prodService.mda4 = false;
    this.env.bucket = keys.aws_bucket;
   this.env.id = keys.aws_id;
   this.env.key = keys.aws_key;
  }

  ngOnInit() {
    window.scrollTo(0,0);
    this.auth.user.subscribe(res=>this.user=res)
  }
  

  goToShow(prod):void {
    let postLink = ['/products', prod.id];
    this.router.navigate(postLink);
  }

  deleteProd(prod:Product){
    if(window.confirm('Вы уверены что хотите удалить этот продукт?')){
      let self = this;
      this.AWS.config.update({region: 'ap-southeast-1', credentials: {"accessKeyId": this.env.id, "secretAccessKey": this.env.key}});
      prod.images.forEach(function(element){
        let s3 = new self.AWS.S3().deleteObject({Bucket: self.env.bucket, Key: element.split('/')[element.split('/').length-2]+'/'+element.split('/')[element.split('/').length-1]},function(err, data) {
              
        }); 
      });
      this.prodService.deleteProd(prod.id)
        .subscribe(res=>{
        },
          err=>alert(err)
        );
    }
  }

  setProd(prod){
    this.prodService.prod.next(prod);
  }

}
