import { Component, OnInit, Inject } from '@angular/core';
import {Router} from "@angular/router";
import {AuthService} from "../services/auth.service";
import {ProductService} from '../services/product.service';
import {AppComponent} from '../../app/app.component';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  constructor(
    public authService:AuthService,
    private router:Router,
    public prodService:ProductService,
    @Inject(AppComponent) public parent: AppComponent

  ){
    this.prodService.mda = true;
    this.prodService.mda2 = false;
    this.prodService.mda3 = false;
    this.prodService.mda4 = false;
  }

  ngOnInit(){
  }

  deleteUser(){
    let self = this;
    if(window.confirm('Вы уверены, что хотите удалить аккаунт?')){
      this.prodService.deleteBucket(this.parent.user.id).subscribe();
  	  this.authService.deleteUser().subscribe(()=>{
      if (localStorage['ognetushitely_user_id']){
        this.authService.fake.next({id:Number(localStorage['ognetushitely_user_id'])});
        this.prodService.getBucket(localStorage['ognetushitely_user_id']).subscribe(res=>{
          this.prodService.bucket.next(res);
        })
      }else{
        this.prodService.newBucket().subscribe(res=>{
          this.authService.fake.next({id:res.user_id});
          localStorage['ognetushitely_user_id'] = res.user_id;
        })
      }
      this.router.navigate(['/']);
      });
    }else{}
  }

  openModal(){
    this.prodService.openModal();
  }

}