import {Component, OnInit} from '@angular/core';
import {ProductService} from '../services/product.service';

@Component({
  selector: 'app-proddialog',
  templateUrl: './proddialog.component.html',
  styleUrls: ['./proddialog.component.css']
})
export class ProddialogComponent implements OnInit {
	

  constructor(public prodService:ProductService) { }

  
  onNewFormResult(e){
    if(e.signedIn)
      this.prodService.closeModal();
    else{
      alert(e.err)
    }
  }

  onEditFormResult(e){
    if(e.signedIn){}
    else{
      alert(e.err)
    }
  }

  ngOnInit() {
  }
  
}
