import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import {Product} from '../objects/product';
import {ProductService} from '../services/product.service';
import {Env} from '../objects/env';
import { Ng2PicaService } from 'ng2-pica';
import * as $ from 'jquery';
import {AuthService} from '../services/auth.service';
import {keys} from 'config';


@Component({
  selector: 'app-productnew',
  templateUrl: './productnew.component.html',
  styleUrls: ['./productnew.component.css']
})
export class ProductnewComponent {

  prod = new Product;
  urls:string[] = [];
  private env = new Env;
  AWS = require('aws-sdk');
  sucess:boolean = false;
  img_upload:boolean = false;
  err:string;
  options;


@Output() onFormResult = new EventEmitter<any>();

  constructor(
    public prodService:ProductService,
    private pic:Ng2PicaService,
    private auth:AuthService
  ){
   this.env.bucket = keys.aws_bucket;
   this.env.id = keys.aws_id;
   this.env.key = keys.aws_key;
   this.AWS.config.update({region: 'ap-southeast-1', credentials: {"accessKeyId": this.env.id, "secretAccessKey": this.env.key}});
   this.options = {
    zIndex: 9999,
    pluginsEnabled: ['align', 'charCounter', 'codeBeautifier', 'codeView', 'colors', 'emoticons', 
    'entities', 'fontAwesome', 'fontFamily', 'fontSize', 'inlineStyle', 'inlineClass', 'lineBreaker', 
    'lineHeight', 'link', 'lists', 'paragraphFormat', 'paragraphStyle', 'quote', 'table', 'url'],
    htmlRemoveTags: ['script', 'style', 'base'],
    events: {
      'froalaEditor.initialized': function (e, editor) {
        $("a:contains('Unlicensed')").parent().css('position', 'absolute');
        $("a:contains('Unlicensed')").parent().css('z-index', '1');
      }
    }
  }
      // this.http.get('https://rocky-brook-32856.herokuapp.com/env').subscribe(data=>{this.env = data.json();});
  }


  createProd(prod:any){
    if(prod.category){prod.category = prod.category.toLowerCase();}
    // if(prod.title ){prod.title = prod.title.toLowerCase();}
    let self = this;
    prod.images = this.urls;
  	this.prodService.createProd(prod).subscribe(res=>{
  		if(res){this.onFormResult.emit({signedIn: true, res});
      this.urls = [];
      this.sucess = false;
      this.img_upload = false;
       res.images.forEach(function(el){
        let s3 = new self.AWS.S3().copyObject({Bucket: self.env.bucket+'/'+res.id, CopySource: self.env.bucket+'/'+el.split('/')[el.split('/').length-1], Key: el.split('/')[el.split('/').length-1]}, function(err, data) {
        
         let s3 = new self.AWS.S3().deleteObject({Bucket: self.env.bucket, Key: el.split('/')[el.split('/').length-1]},function(err, data) {
             
         });    
      })
     })
     
    }},
  	err=>{
  		console.log('err:', err);
        this.onFormResult.emit({signedIn: false, err});
  	})
  }

  getFile(fileInput:any){
    let self = this;
    let file = fileInput.target.files;
    for (let i=0; i < file.length; i++){
      this.pic.resize([file[i]], 800, 800,true).subscribe(res=>this.fileEvent(res));
    }
  }

  fileEvent(data:any){
    this.sucess = false;
    this.img_upload = true;
    let self = this;
    let params = {Bucket: this.env.bucket, Key: this.makeid(), Body: data};
    let s3 = new this.AWS.S3.ManagedUpload({params: params});
    s3.on('httpUploadProgress', function(evt) {
      $('#pus').css('width','0%');
      $('#pus').css('width',evt.loaded*100/evt.total+'%');
    }).send(function(error, s3res) { 
      if (error) {self.err = error.message;}else{self.sucess = true;};
      let a = self.urls.length;
      self.urls[a] = 'https://s3-ap-southeast-1.amazonaws.com/blabucket2018/'+s3res.Key;
    });
  }

  getFile2(fileInput:any){
    let self = this;
    let file = fileInput.target.files[0];
    // console.log(file);
    this.sucess = false;
    this.img_upload = true;
    let params = {Bucket: this.env.bucket, Key: 'price.xlsx', Body: file};
    let s3 = new self.AWS.S3().deleteObject({Bucket: self.env.bucket, Key: "price.xlsx"},function(err, data) {
      let s3 = new self.AWS.S3.ManagedUpload({params: params});
      s3.on('httpUploadProgress', function(evt) {
        $('#pus').css('width','0%');
        $('#pus').css('width',evt.loaded*100/evt.total+'%');
      }).send(function(error, s3res) { 
        if (error) {self.err = error.message;}else{self.sucess = true;};
      });   
    }); 
  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 20; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }

  delFromUpl(idx){
    let self = this;
    let s3 = new this.AWS.S3().deleteObject({Bucket: this.env.bucket, Key: this.urls[idx].split('/')[this.urls[idx].split('/').length-1]},function(err, data) {
      if (data){;self.urls.splice(idx, 1);}else{console.log(err)}
    }); 
    
  }

}


