import { Component } from '@angular/core';
import {trigger, animate, transition, style} from '@angular/animations';
import {ProductService} from './services/product.service';
import {AuthService} from './services/auth.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
     trigger(
      'enterAnimation', [
        transition('void => *', [
          style({opacity: 1}),
          animate('600ms', style({opacity: 1}))
        ]),
        transition('* => void', [
          style({opacity: 1}),
          animate('600ms', style({opacity: 0}))
        ])
      ]
     )]
})
export class AppComponent {

  bla:boolean = false;
  public prods:any[] = [];
  public arr = [];
  public count;
  public clone = [];
  public bucket = null;
  public cats = null;
  public fake = null;
  public user = null;
  public sum = null;
  public bucket_numb = null;
  public prodArr = [];
  public news = null;
  public prod = null;
  public catItems = null;
  public category = null;

	constructor(
    private prodService:ProductService,
    private authService:AuthService
  ){
    this.prodService.catItems.subscribe(res=>{
      if(res!=null){
        this.catItems = res;
        this.category = res;
        this.prods = this.clone;
        this.prods = this.prods.filter(item =>{ return item.attributes.category.toLowerCase() == this.catItems.toLowerCase()});
      }else{
        this.prods = this.clone;
      }
    })
    let self = this;
    this.prodService.cats.subscribe(res=>{
        this.cats = res;
    })
    this.prodService.prod.subscribe(res=>this.prod=res)
    this.authService.fake.subscribe(res=>this.fake=res)
    this.prodService.bucket.subscribe(res=>{
      if(res!=null){
        this.bucket = res;
        this.sum = 0;
        this.bucket_numb = 0;
        this.prodArr = [];
        if(this.bucket.product_id && Object.keys(this.bucket.product_id).length>0){
          let keys = Object.keys(this.bucket.product_id);
          keys.forEach(el=>{
            let prod = this.prods.filter(elem=>{return elem.id == el})
            this.prodArr.push([prod[0],this.bucket.product_id[el]])
            this.bucket_numb = this.bucket_numb + this.bucket.product_id[el];
            if(prod[0].attributes.price){
              this.sum = this.sum + prod[0].attributes.price*this.bucket.product_id[el]
            }
          })
        }
      }
    })
    this.authService.user.subscribe(res=>{
        this.user = res;
    })
    this.prodService.news.subscribe(res=>{
        this.news=res
    });
     this.authService.validateToken().subscribe(
        (res) => {
          if (res.success){
            // this.authService.userSignedIn$.next(true);
            this.authService.user.next(this.authService.userData());
            if(this.authService.userType() == "ADMIN"){
              this.user.type = true
              this.authService.user.next(this.user);
            }else{
              this.user.type = false;
              this.authService.user.next(this.user);
              this.authService.fake.next(null);
              this.prodService.getBucket(this.user.id).subscribe(res=>{
                this.prodService.bucket.next(res);
              })}
          }
    },(err) =>{
      if (localStorage['ognetushitely_user_id']){
        this.fake = {id:Number(localStorage['ognetushitely_user_id'])};
        this.prodService.getBucket(this.fake.id).subscribe(res=>{
              if (res["id"]){
                this.prodService.bucket.next(res);
              }else{
                this.fake = {id:res};
                localStorage['ognetushitely_user_id'] = res;
                this.prodService.getBucket(this.fake.id).subscribe(res=>{
                  this.prodService.bucket.next(res);
                })
              }
        })
      }else{
        this.prodService.newBucket().subscribe(res=>{
          this.fake = {id:res};
          localStorage['ognetushitely_user_id'] = res;
          this.prodService.getBucket(this.fake.id).subscribe(res=>{
            this.prodService.bucket.next(res);
          })
        })
      }
    })
    this.prodService.getProds().subscribe(res=>{
      this.bla = true;
      this.prodService.cats.next(res.cats);
      this.prods = JSON.parse(res.prods).data;
      this.prodService.getNews().subscribe(res=>this.prodService.news.next(res));
      this.prodService.countDB = Math.ceil(res.count/50);
      this.count = res.count;
      for(let i=2;i<Math.ceil(res.count/50)+1;i++){
        this.prodService.getFirst(i).subscribe(res=>{
          self.prods = self.prods.concat(res.data); 
          self.clone = self.prods;
        })
      }
    })
	}

}