import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'itemNumber',
  pure: false
})

 

export class ItemNumber implements PipeTransform {

  public transform(value:any[],start:number): any[] {
  	// let arr:any[] = [];
  	// for(let i = 0; i < 18; i++){
  	// 	arr[i] = value[start];
  	// 	start = start + 1;
  	// }
  	// return arr;
  	return value.slice(0, start+18);
  }
}